import React from 'react';
import { Box, Paper, Typography, useTheme } from '@mui/material';
import { alpha } from '@mui/material/styles';

interface StatCardProps {
  title: string;
  value: string | number;
  icon: React.ReactNode;
  trend?: number;
  color?: string;
  sx?: object;
}

const StatCard: React.FC<StatCardProps> = ({ 
  title, 
  value, 
  icon, 
  trend, 
  color = 'primary',
  sx = {} 
}) => {
  const theme = useTheme();

  return (
    <Paper
      elevation={0}
      sx={{
        p: 3,
        height: '100%',
        borderRadius: 3,
        position: 'relative',
        overflow: 'hidden',
        background: 'rgba(255, 255, 255, 0.9)',
        backdropFilter: 'blur(8px)',
        border: '1px solid',
        borderColor: alpha(theme.palette[color].main, 0.1),
        transition: 'transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out',
        '&:hover': {
          transform: 'translateY(-4px)',
          boxShadow: `0 8px 20px ${alpha(theme.palette[color].main, 0.15)}`,
        },
        ...sx
      }}
    >
      {/* 背景装饰 */}
      <Box
        sx={{
          position: 'absolute',
          top: 0,
          right: 0,
          width: '30%',
          height: '100%',
          background: `linear-gradient(45deg, transparent, ${alpha(theme.palette[color].main, 0.03)})`,
          borderLeft: `1px solid ${alpha(theme.palette[color].main, 0.05)}`,
        }}
      />

      <Box sx={{ position: 'relative', zIndex: 1 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
          <Box
            sx={{
              p: 1,
              borderRadius: 2,
              display: 'flex',
              bgcolor: alpha(theme.palette[color].main, 0.1),
              color: theme.palette[color].main,
            }}
          >
            {icon}
          </Box>
          {trend !== undefined && (
            <Typography
              variant="subtitle2"
              sx={{
                display: 'flex',
                alignItems: 'center',
                color: trend >= 0 ? theme.palette.success.main : theme.palette.error.main,
                bgcolor: trend >= 0 
                  ? alpha(theme.palette.success.main, 0.1)
                  : alpha(theme.palette.error.main, 0.1),
                px: 1.5,
                py: 0.5,
                borderRadius: 2,
                fontSize: '0.75rem',
                fontWeight: 600,
              }}
            >
              {trend >= 0 ? '+' : ''}{trend}%
            </Typography>
          )}
        </Box>

        <Typography
          variant="h4"
          sx={{
            mb: 1,
            fontWeight: 700,
            color: theme.palette.text.primary,
            fontSize: { xs: '1.5rem', sm: '2rem' },
          }}
        >
          {value}
        </Typography>

        <Typography
          variant="subtitle2"
          sx={{
            color: alpha(theme.palette.text.primary, 0.7),
            fontWeight: 500,
          }}
        >
          {title}
        </Typography>
      </Box>
    </Paper>
  );
};

export default StatCard; 